.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    margin: 0 37%;
    margin-top: 0px;
    border-radius: 6px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 3%;
    margin-top: 26vh;
    &>label{
        margin-bottom: 6px;
    }
    &>input{
        margin-bottom: 6px;
        padding: 4px;
        border-radius: 6px;
        border: 3px solid lightseagreen;
    }
    &>button{
        padding: 6px;
        background-color: blueviolet;
        color: white;
        font-size: 18px;
        border-radius: 7px;
    }
    @media screen and (max-width:1105px) {
        margin: 0 33%;
        margin-top: 26vh;
    }
    @media screen and (max-width:760px){
        margin: 0 27%;
        margin-top: 26vh;
    }
    @media screen and (max-width:560px){
        margin: 0 15%;
        margin-top: 26vh;
    }
    @media screen and (max-width:300px){
        margin: 0 3%;
        margin-top: 26vh;
    }
}