.fordropdown{
    position: absolute;
    top: 100%;
    width: 100%;
    max-height: 233px;
    overflow: auto;
    z-index: 10000;
}
.forwholediv{
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 0 11%;
}
.fordropdownelement{
    width: 100%;
    cursor: pointer !important;
    background-color: blueviolet;
    border: 1px solid;
    color: white;
    white-space: pre;
    text-align: left;
    width: 98%;
    margin: 0 1%;
    &:hover{
        background-color: white;
        color: blueviolet;
    }
}
.forelementwithlabel{
    display: flex;
    flex-direction: column;
    &>input{
        padding: 13px;
        border-radius: 6px;
        margin: 1%;
        border-color: brown;
        border-width: 4px;
        margin-bottom: 0px;
    }
}
