.forcontainer{
    display: flex;
    flex-direction: column;
    overflow: auto;
    width: 100%;
    height: 78vh;
}
.forsender{
    background-color: deepskyblue;
    margin: 1% 1%;
    align-self: end;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 6px;
    margin-right: 6px;
    padding: 4px 13px;
}
.forreceiver{
    background-color: white;
    margin: 1% 1%;
    align-self: start;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 6px;
    margin-left: 6px;
    padding: 4px 13px;
}
.forsendcontainer{
    padding: 6px;
    background-color: dodgerblue;
    grid-template-columns: 87% 10%;
    display: grid;
    column-gap: 3%;
    &>input{
        border-radius: 23px;
        width: 100%;
        padding: 6px;
    }
    &>button{
        border-radius: 6px;
        padding: 5px;
        width: 100%;
    }
}
